*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-image: url('/src/style/bg.png');
    background-repeat: no-repeat;
    background-position: top center;
}

.container{
    max-width: 700px;
    width: 100%;
    margin: auto;
    margin-top: 70px;

    background-color: rgb(100, 100, 100);
    padding: 15px;
}

.containerTimer{
    margin-top: 40px;
}

.containerCommissionTitle, .containerTitle{
    display: flex;
    margin: 5px 0;
    justify-content: space-between;
}

.containerCommissionTitleAvailable{
    display: flex;
}

.containerCommissionTitleEvent{
    display: flex;
    align-items: center;
    margin-left: 15px;
}

.containerCommissionTitleEvent input{

    margin-left: 5px;
}

.availableTimer{
    display: flex;
    margin: 5px 0;
    justify-content: center;
}

.addTimer{
    width: 100%;
}

/*! timer.jsx !*/
.timer{
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}